        font-weight: bolder;
        padding-top: 5rem;
    }
    #about-me-p {
        line-height: 2.4rem;
        letter-spacing: .08rem;
        margin-top: 2.8rem;
        color: #eee;
    }
    .about-me-wrapper {
        display: grid;
        gap: 4rem;
        grid-template-columns: 40% 60%;
        grid-auto-rows: minmax(200px, auto);
    }
    .technical-skills {
        margin-top: 2.8rem;
        background: linear-gradient(#f1f1f1, #eef9ff);
        border-radius: 2rem;
        box-shadow: 0 8px 16px -8px #2a0c3d;
