    }
    .technical-skills div {
        display: grid;
        grid-template-columns: 1fr;
    }
    .technical-skills .frontEnd {
        border-bottom: none;
        border-right: 2px dashed #bbb;
    }
    .technical-skills div .languages {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .technical-skills div div div img {
        height: 60px;
        width: 60px;
    }
    #projectsSection {
        position: relative;
        margin-bottom: 3rem;
    }
    #projectsSection h4 {
        border-bottom: 1px solid lightgray;
        letter-spacing: .5rem;
        color: #513662;
        padding-bottom: 1.5rem;
        margin-bottom: 2rem;
        font-weight: bolder;
        padding-top: 4rem;
    }
    .skills-border {
        border-left: 4px dotted rgb(74, 84, 88);
        border-right: 4px dotted rgb(74, 84, 88);
        text-align: center;
    }
    /* TIMELINE */
    .timeline {
        list-style: none;
        padding: 20px 0 20px;
        position: relative;
        z-index: 4;
    }
    .timeline:before {
        top: 0;
        bottom: 0;
        position: absolute;
        content: " ";
        width: 3px;
        background-color: rgb(255, 255, 255);
        left: 50%;
        margin-left: -1.5px;
    }
    .timeline>li {
        margin-bottom: 20px;
        position: relative;
    }
    .timeline>li:before,
    .timeline>li:after {
        content: " ";
        display: table;
    }
    .timeline>li:after {
        clear: both;
    }
    .timeline>li:before,
    .timeline>li:after {
        content: " ";
        display: table;
    }
    .timeline>li:after {
        clear: both;
    }
    .timeline>li>.timeline-panel {
        width: 46%;
        float: left;
        border-radius: 2px;
        padding: 20px;
        position: relative;
        -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.212);
        box-shadow: 0 1px 6px rgba(0, 0, 0, 0.212);
        background-color: white;
    }
    .timeline>li>.timeline-panel:before {
        position: absolute;
        top: 26px;
        right: -15px;
        display: inline-block;
        border-top: 15px solid transparent;
        border-left: 15px solid rgb(255, 255, 255);
        border-right: 0 solid rgb(255, 255, 255);
        border-bottom: 15px solid transparent;
        content: " ";
    }
    .timeline>li>.timeline-panel:after {
        position: absolute;
        top: 27px;
        right: -14px;
        display: inline-block;
        border-top: 14px solid transparent;
        border-left: 14px solid #fff;
        border-right: 0 solid #fff;
        border-bottom: 14px solid transparent;
        content: " ";
    }
    .timeline>li>.timeline-badge {
        color: #fff;
        width: 54px;
        height: 54px;
        line-height: 50px;
        font-size: 1.4em;
        text-align: center;
        position: absolute;
        top: 16px;
        left: 50%;
        margin-left: -25px;
        background-color: #999999;
        z-index: 100;
        border-top-right-radius: 50%;
        border-top-left-radius: 50%;
        border-bottom-right-radius: 50%;
        border-bottom-left-radius: 50%;
    }
    .timeline>li.timeline-inverted>.timeline-panel {
        float: right;
    }
    .timeline>li.timeline-inverted>.timeline-panel:before {
        border-left-width: 0;
        border-right-width: 15px;
        left: -15px;
        right: auto;
    }
    .timeline>li.timeline-inverted>.timeline-panel:after {
        border-left-width: 0;
        border-right-width: 14px;
        left: -14px;
        right: auto;
    }
    .timeline-badge.first {
        background-color: #512d68 !important;
    }
    .timeline-badge.second {
        background-color: #48306e !important;
    }
    .timeline-badge.third {
        background-color: #2f3079 !important;
    }
    .timeline-badge.fourth {
        background-color: #2f4379 !important;
    }
    .timeline-badge.fifth {
        background-color: #2c4c77 !important;
    }
    .timeline-title {
        margin-top: 0;
        color: inherit;
    }
    .timeline-body>p,
    .timeline-body>ul {
        margin-bottom: 0;
    }
    .timeline-body>p+p {
        margin-top: 5px;
    }
}

